import React, { useState } from "react";
import "../assets/css/main.css";
import Gif from "../assets/images/Gif.png";
import StarIcon from "../assets/images/StarIcon.png";

const AccordionItem = ({ title, content, isActive, onToggle }) => {
    return (
        <div className={`accordion__item ${isActive ? "active" : ""}`}>
            <button className="accordion__header" onClick={onToggle}>
                {title}
                <svg className="arrow" width="14" height="8" viewBox="0 0 14 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" clipRule="evenodd" d="M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z" fill="white" />
                </svg>
            </button>
            <div className="accordion__content" style={{ maxHeight: isActive ? content.scrollHeight + "px" : "0", paddingTop: isActive ? "15px" : "0", paddingBottom: isActive ? "15px" : "0" }}>
                {content}
            </div>
        </div>
    );
};

const platformCards = [
    { title: "Новичкам", text: "Если у вас нет опыта в веб-разработке, наш простой редактор поможет создать сайт за несколько минут." },
    { title: "Блогерам", text: "Ведите личный блог, делитесь статьями и создавайте портфолио с удобным дизайном." },
    { title: "Фрилансерам", text: "Создавайте сайты-визитки, портфолио и лендинги, чтобы привлекать клиентов." },
    { title: "Малому бизнесу", text: "Создайте сайт для своей компании, кафе, студии или сервиса без лишних затрат." },
    { title: "Проектам", text: "Преподаватели и студенты могут использовать платформу для учебных сайтов и проектов." },
    { title: "Стартапам", text: "Быстро запустите сайт для продвижения идей и привлечения аудитории." },
];

const Main = () => {
    const [activeIndex, setActiveIndex] = useState(null);

    const toggleAccordion = (index) => {
        setActiveIndex(activeIndex === index ? null : index);
    };

    return (
        <div className="wrapper">
            <header className="header">
                <div className="header__logo">SiteFlow</div>
                <nav className="header__nav">
                    <ul className="header__menu">
                        <li className="header__menu-item"><a href="#">Главная</a></li>
                        <li className="header__menu-item"><a href="#">О проекте</a></li>
                        <li><button className="button button--primary">Начать работу</button></li>
                    </ul>
                </nav>
            </header>

            <div className="hero">
                <h1 className="hero__title">Создай сайт с<br/> SiteFlow</h1>
                <p className="hero__description">Выбирайте шаблон, настраивайте дизайн, добавляйте<br/> контент — всё просто и интуитивно.</p>
                <div className="hero__buttons">
                    <button className="button button--primary">Создать сайт</button>
                    <button className="button button--secondary">Узнать подробнее</button>
                </div>
            </div>
            <div className="features">
                <h2 className="features__title">Интуитивный интерфейс – редактируйте сайт в пару кликов</h2>
                <img className="features__image" src={Gif} alt="InterfacePreview" />
            </div>
            <div className="platform">
                <div className="platform__intro">
                    <img className="icon icon--star" src={StarIcon} alt="StarIcon" />
                    <h2 className="platform__title">Кому подойдёт наша платформа?</h2>
                </div>
                <div className="platform__cards">
                    {platformCards.map((card, index) => (
                        <div className="platform__card" key={index}>
                            <h3 className="platform__card-title">{card.title}</h3>
                            <p className="platform__card-text">{card.text}</p>
                        </div>
                    ))}
                </div>
                <button className="button button--primary platform__button">
                    Перейти в редактор
                    <svg className="arrow" width="14" height="8" viewBox="0 0 14 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fillRule="evenodd" clipRule="evenodd" d="M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z" fill="white" />
                    </svg>
                </button>
            </div>
            <div className="faq">
                <h2 className="faq__title">FAQ</h2>
                <div className="accordion">
                    <AccordionItem 
                        title="Для кого подходит ваш сайт?"
                        content="Наш сайт подходит для всех, кто хочет быстро создать свой веб-ресурс без программирования."
                        isActive={activeIndex === 0}
                        onToggle={() => toggleAccordion(0)}
                    />
                    <AccordionItem 
                        title="Смогу ли я создать сайт с нуля?"
                        content="Да, наша платформа позволяет создавать сайты с нуля без технических навыков."
                        isActive={activeIndex === 1}
                        onToggle={() => toggleAccordion(1)}
                    />
                    <AccordionItem 
                        title="Как экспортировать код?"
                        content="Вы можете экспортировать код вашего сайта в настройках проекта."
                        isActive={activeIndex === 2}
                        onToggle={() => toggleAccordion(2)}
                    />
                </div>
            </div>

            <footer className="footer">
                <div className="footer__container">
                    <h2 className="footer__logo">SiteFlow</h2>
                    <nav className="footer__menu">
                        <a href="#">Главная</a>
                        <a href="#">Редактор</a>
                        <a href="#">О нас</a>
                        <a href="#">Контакты</a>
                        <a href="#">Блог</a>
                    </nav>
                    <p className="footer__text">©2025 | This platform is made with <span className="heart">♥</span> by <a href="#">Alina and Alexey</a></p>
                </div>
            </footer>
        </div>
    );
};

export default Main;